
const BASE_URL = `https://api.coinpaprika.com/v1`;

export async function fetchCoins() {
    // return fetch(`${BASE_URL}/coins`).then((response) => response.json());
    // return fetch(`https://api.coinpaprika.com/v1/coins`).then((response) => response.json());
    const json = await (await fetch(`https://api.coinpaprika.com/v1/coins`)).json();
    return json;
  }

export async function fetchCoinInfo(coinId: string) {
    // return fetch(`${BASE_URL}/coins/${coinId}`).then((response) =>
    // return fetch(`https://api.coinpaprika.com/v1/coins/${coinId}`).then((response) =>
      // response.json()
    const json = await (await fetch(`https://api.coinpaprika.com/v1/coins/${coinId}`)).json();
    return json;
  }
  
  export async function fetchCoinTickers(coinId: string) {
    // return fetch(`${BASE_URL}/tickers/${coinId}`).then((response) =>
    // return fetch(`https://api.coinpaprika.com/v1/tickers/${coinId}`).then((response) =>
    const json = await (await fetch(`https://api.coinpaprika.com/v1/tickers/${coinId}`)).json();
     return json;
  }

  
export function fetchCoinHistory(coinId: string) {
  const endDate = Math.floor(Date.now() / 1000);
  const startDate = endDate - 60 * 60 * 24 * 7 * 2;
  return fetch(
    // `${BASE_URL}/coins/${coinId}/ohlcv/historical?start=${startDate}&end=${endDate}`
    `https://api.coinpaprika.com/v1/coins/${coinId}/ohlcv/historical?start=${startDate}&end=${endDate}`
  ).then((response) => response.json());
}